.modal {
  @include centrFlex;

  position: fixed;
  padding: 20px 20px 48px 20px;
  top: 0;
  left: 0;
  // opacity: 0;
  width: 100vw;
  height: 100vh;
  transform: scale(1.1);
  transition: transform var(--timingFunction);
  overflow-y: auto;

  @media screen and (max-width: 767px) {
    align-items: flex-start;
  }
}

.modal-backdrop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;

  background-color: #000000;
  opacity: 0.25;
}

body.show-modal .backdrop {
  opacity: 1;
  visibility: visible;
  pointer-events: initial;
}

body.show-modal .modal {
  transform: scale(1);
}

.modal-wrapper {
  position: relative;
  padding: 48px 20px 40px 20px;
  width: 280px;

  background-color: var(--primaryLightThemeBgColor);

  @include tablet {
    width: 704px;
    padding: 40px 72px 40px 36px;
  }

  @include desktop {
    width: 806px;
    padding: 50px 12px 40px 12px;
  }
}

.modal-btn {
  @include centrFlex;

  position: absolute;
  top: 8px;
  right: 8px;
  width: 30px;
  height: 30px;

  background-color: transparent;
  cursor: pointer;
  transition: color var(--timingFunction);

  &:hover {
    color: var(--accentBtnColor);
  }
}

.modal__icon {
  right: 8px;
  top: 8px;
  font-size: 26px;
  color: var(--primaryTextDark);

  &:hover,
  &:focus {
    color: #ff6b08;
  }
  margin: auto;
}

.film-details {
  @include tablet {
    @include flex;
    align-items: flex-start;
  }
}

.film-details__image {
  width: 240px;
  height: 357px;
  border-radius: 5px;

  @media screen and (max-width: 767px) {
    margin-bottom: 20px;
  }

  @include tablet {
    width: 264px;
    height: 374px;
    margin-right: 68px;
  }

  @include desktop {
    width: 375px;
    height: 478px;
    margin-right: 16px;
    flex-shrink: 0;
    object-fit: cover;
  }
}

.film-details__title {
  margin-bottom: 20px;

  font-weight: 500;
  font-style: normal;
  font-size: 20px;
  line-height: 1.17;
  text-transform: uppercase;

  @include desktop {
    font-size: 30px;
  }
}

.film-details__list {
  margin-bottom: 20px;
}

.film-details__item {
  @include flex;
  margin-bottom: 8px;
}

.film-details__text {
  min-width: 109px;

  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  color: var(--secondaryTextDetailsColor);

  &:last-child {
    margin-bottom: 0;
  }

  @include desktop {
    min-width: 152px;
  }
}

.film-details__span {
  @include centrFlex;

  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;

  &--accent {
    width: 36px;
    height: 16px;
    margin-right: 2px;

    background-color: var(--accentBtnColor);
    color: var(--primaryTextLight);
    border-radius: 5px;
  }

  &--noaccent {
    width: 36px;
    height: 16px;
    margin-left: 2px;

    background-color: var(--primaryLightThemeBgColor);
    color: var(--primaryTextDark);
    border-radius: 5px;
  }
}

.film-details__about {
  margin-bottom: 8px;

  font-weight: 500;
  font-size: 12px;
  line-height: 1.33;
  text-transform: uppercase;
  color: var(--primaryTextDark);
}

.film-details__overview {
  margin-bottom: 20px;

  font-weight: 500;
  font-size: 12px;
  line-height: 1.67;
  color: var(--primaryTextDark);
}

.film-details-btn__list {
  display: flex;
}

.film-details-btn__item {
  margin-right: 15px;

  &:last-child {
    margin-right: 0;
  }
}

.film-details-btn {
  width: 112px;
  height: 44px;
  text-align: center;
  text-transform: uppercase;
  border-radius: 5px;
  color: var(--primaryTextDark);
  background-color: var(--primaryLightThemeBgColor);
  cursor: pointer;
  border: 1px solid var(--primaryTextDark);
  transition: background-color var(--timingFunction),
    border-color var(--timingFunction), transform var(--timingFunction);

  &.active,
  &:focus,
  &:hover {
    border: 1px solid var(--accentBtnColor);
    background-color: var(--accentBtnColor);
    color: #ffffff;
    box-shadow: 0px 8px 43px rgba(255, 107, 1, 0.6);
  }

  @include tablet {
    width: 125px;
  }
  @include desktop {
    width: 136px;
  }
}
